import React, { Component } from 'react';
import PaymentsMethod from '../PaymentsMethod/PaymentsMethod';
import ApiUsers from '../../api/ApiUsers';
import AuthUtil from '../../util/AuthUtil';

class Profile extends Component {

    constructor(props) {
        super(props)

        this.state = {
            data: AuthUtil.getUserInfo(),
            errorMsg: ""
        };
    }

    componentDidMount() {

        ApiUsers.getUsers((response) => {
            this.setState({ data: response })
        }, (error) => {
            this.setState({ errorMsg: error.message })
        })
        
    }

    render() {

        return (
            <div>
                <div className="col-md-6 col-lg-6 offset-lg-3 pt-5">
                    <div className="row">
                        <img className="imgProfile image-cropper-profileImg float-left" alt="imagem de perfil" src={this.state.data.profilePicturePath} />
                        <div className="form-group pt-5">
                        <span className="px-3">{this.state.data.firstName + " " + this.state.data.lastName}</span>
                        </div>
                        <div className="form-group pt-5">
                        <span className="px-3">{this.state.data.firstName + " " + this.state.data.lastName}</span>
                        </div>
                        <div className="form-group pt-5">
                        <span className="px-3">{this.state.data.firstName + " " + this.state.data.lastName}</span>
                        </div>
                    </div>
                </div>
                <hr />
                <div className="row">
                    <PaymentsMethod />
                </div>
            </div>
        )
    }
}

export default Profile


