import React, { Component } from 'react';
import MetaTags from 'react-meta-tags';
import ApiHelper from '../../api/ApiHelper';
import Map from '../Map/Map'
import Point from '../Point/Point'
import PointDetail from '../PointDetail/PointDetail';
import Points from '../Points/Points'
import axios from 'axios';
import { Link } from 'react-router-dom';


class Delivery extends Component {

    constructor(props) {
        super(props)
        this.state = {
            showMarker: false,
            pontos: [],
            flag: 'include',
            flagEstado: 'progress',
            endereco: "",
            numero: "",
            complemento: "",
            lat: "",
            lng: "",
            ponto: 0,
            opcaoRetirarDescricao: "",
            notificacaoSmsEmail: "",
            otimizaRota: false,
            osGerada: "",
            loading: true,
            content: '',
            changeComponent: 1,
            tipoEntrega: ''

        }

        this.domain = "https://api.loghero.com.br/v1/"
        this.config = {
            headers: { 'x-api-key': 'XhaPTJDpGh9aUWN1M2CEG27UWWB7tRWt48OfAuvK' }
        }

        this.handleChangeEndereco = this.handleChangeEndereco.bind(this)
    }

    getRandomInt(min, max) {
        min = Math.ceil(min)
        max = Math.floor(max)
        return Math.floor(Math.random() * (max - min)) + min
    }


    addWorkOrder() {
        var workOrderXId = (this.getRandomInt(1, 99999))

        var workOrder = {
            partnerHubXId: 425,
            partnerCourrierXId: 0,
            userId: 1,
            optimizeRoute: false,
            requestedEquipmentType: "Moto",
            partnerWorkOrderXId: workOrderXId,
            partnerHubName: "EXP. HELPIE TECNOLOGIA LTDA",
            partnerHubCreatedDate: "2017-12-07T10:00:00+0000",
            partnerHubMaxAccept: "2017-11-07T10:00:00+0000",
            partnerHubDispatch: "2017-11-07T150:00:00+0000",
            partnerWorkOrderSequence: 1,
            points: [
                {
                    lat: -23.486874,
                    lng: -46.857264,
                    address1: "Av. Andrômeda",
                    addressNumber: "5374",
                    address2: "",
                    neighborhood: "Alphaville Empresarial",
                    city: "São Paulo",
                    state: "SP",
                    country: "Brasil",
                    zipCode: "04020-060",
                    action: "ação",
                    instructions: "Pegar Documento com Liliane do depto de mkt. ramal 243. Falar com Janaice",
                    signatureRequired: true,
                    photoRequired: true,
                    federalId: "111.111.111-11",
                    venueDescription: "Fulano de tal",
                    sequence: 1,
                    contact: "Contato 1",
                    partnerPointXId: 1,
                    partnerRequestedSequence: 1,
                    partnerRequesterName: "Solicitante 1",
                    partnerRequestXId: 1,
                    partnerHubArrivalForecast: "2017-11-21T15:00:00+0000"
                },
                {
                    lat: -23.636171,
                    lng: -46.630558,
                    address1: "Rua Basil Cameron",
                    addressNumber: "48b",
                    address2: "",
                    neighborhood: "Vila Guarani",
                    city: "Sao Paulo",
                    state: "SP",
                    country: "Brasil",
                    zipCode: "04316-025",
                    action: "Entregar Documento", //passar como parametro para envio para API
                    instructions: "Entregar Documento para Josóe Carlos na portaria",
                    signatureRequired: true,
                    photoRequired: true,
                    federalId: "111.111.111-11",
                    venueDescription: "Fulano de tal",
                    sequence: 2,
                    contact: "Contato 2",
                    partnerPointXId: 2,
                    partnerRequestedSequence: 1,
                    partnerRequesterName: "Solicitante 2",
                    partnerRequestXId: 1,
                    partnerHubArrivalForecast: "2017-11-21T15:00:00+0000"
                }
            ]
        }


        axios.post(this.domain + 'workorders', workOrder, this.config)
            .then(response => {
                this.setState({ osGerada: response.data.id, loading: false });
                // History.forceUpdate();
            })
            .catch((error) => {
                console.log(JSON.stringify(error));
            })
    }


    getQuotation() {
        console.log(this.state.pontos.length)
        if (this.state.pontos.length > 1) {

            this.setState({
                price: "",
                distance: "",
                time: ""
            })


            // console.log("Quotation");
            var points = {
                "points": [
                    {
                        "lat": this.state.pontos[0].lat,
                        "lng": this.state.pontos[0].lng
                    },
                    {
                        "lat": this.state.pontos[1].lat,
                        "lng": this.state.pontos[1].lng
                    }
                ]
            }

            var self = this

            axios.post(this.domain + 'quotation', points, this.config)
                .then(function (response) {
                    console.log(response);
                    self.setState({
                        price: response.data.priceFormatted,
                        distance: response.data.distanceFormatted,
                        time: response.data.timeFormatted,
                        loading: false
                    })
                })
                .catch(function (error) {
                    console.log(error)
                })
        }
    }

    handleDeleteEndereco = (pontoAtual) => {

        let arrayPontos = this.state.pontos

        let deletePontos = this.state.pontos.slice() //copy the array

        console.log("delete: " + pontoAtual);


        deletePontos.splice(pontoAtual, 1)


        if (arrayPontos.length < 3) {

            this.setState({
                price: "",
                distance: "",
                time: ""
            })
        }

        this.setState({
            pontos: deletePontos,
            flag: 'include',
            endereco: "",
            complemento: "",
            opcaoRetirarDescricao: "",
            tipoEntrega: "",
            notificacaoSmsEmail: ""
        }, () => this.getQuotation())

        this.map.forceUpdate();

        // console.log(this.state.flag);
    }

    addPonto = () => {

        console.log(this.state.numero);

        console.log(this.state.flag)
        //adicionar a condição se tiver numero

        var exists = this.state.pontos.some(item => item.ponto === this.state.ponto)
        if (!exists) {
            console.log("OPA3:" + this.state.numero);
            if (this.state.numero !== 0) {
                this.state.pontos.push({
                    ponto: this.state.ponto,
                    endereco: this.state.endereco,
                    complemento: this.state.complemento,
                    lat: this.state.lat,
                    lng: this.state.lng,
                })

                console.log(this.state.pontos);


                this.setState({ flag: "edit" })

                this.setState(this.state)
            }

        } else {
            this.editPoint(this.state.ponto, this.state.endereco,
                this.state.complemento, this.state.lat, this.state.lng, this.state.opcaoRetirarDescricao,this.state.tipoEntrega,this.state.notificacaoSmsEmail)
        }
        if (this.state.numero !== 0) {
            this.map.forceUpdate();
        }

    }

    // this.getQuotation()




    editPoint(id, endereco, complemento, lat, lng, opcaoRetirarDescricao, tipoEntrega, notificacaoSmsEmail) {


        let editPontos = this.state.pontos.slice()

        var point = {
            ponto: id,
            endereco: endereco,
            complemento: complemento,
            lat: lat,
            lng: lng,
            opcaoRetirarDescricao: opcaoRetirarDescricao,
            tipoEntrega: tipoEntrega,
            notificacaoSmsEmail: notificacaoSmsEmail
        }
        editPontos.splice(id, 1, point)
        this.setState({ pontos: editPontos })

        this.getQuotation();
    }

    handleChangeFlagEdit = (pontoAtual) => {
        // console.log(pontoAtual);

        this.setState({ ponto: pontoAtual }, this.setState({ flag: 'edit' }))
        var pontoEdicao = this.state.pontos[pontoAtual]
        // console.log(JSON.stringify(pontoEdicao));

        this.setState({
            endereco: pontoEdicao.endereco, complemento: pontoEdicao.complemento, lat: pontoEdicao.lat, lng: pontoEdicao.lng,
            opcaoRetirarDescricao: pontoEdicao.opcaoRetirarDescricao, tipoEntrega: pontoEdicao.tipoEntrega, notificacaoSmsEmail: pontoEdicao.notificacaoSmsEmail
        })

        this.setState({ changeComponent: 1 });


    }

    handleChangeFlagEstado = () => {
        // console.log(pontoAtual);

        this.setState({ flagEstado: 'done' })

    }

    handleChangeEndereco(endereco, lat, lng, numero) {
        this.setState(({
            lat: lat,
            lng: lng,
            showMarker: true,
            endereco: endereco,
            numero: numero
        }), () => this.addPonto())

        this.getQuotation();

    }

    // handleChangeNumero(numero) {
    //     this.setState(({
    //         numero: numero
    //     }), () => this.addPonto())        
    //     console.log(this.state.pontos); 
    // }



    handleChangeComplemento = (e) => {
        this.setState({ ...this.state, complemento: e.target.value })
    }


    handleChangeTeste(testeFinal) {
        this.setState({
            acao: testeFinal
        })
    }

    handleClickButtonAddPonto = (e) => {

        // console.log("teste")
        // this.addPonto()

    }

    handleClickButtonClearState = (proximoPonto) => {


        this.setState({
            flag: 'include',
            flagEstado: 'progress',
            endereco: "",
            complemento: "",
            opcaoRetirarDescricao: "",
            tipoEntrega: "",
            notificacaoSmsEmail: "",
            ponto: proximoPonto
        })

        // console.log(this.state.flag);

    }

    handleClickButtonAddWorkOrder = () => {
        this.addWorkOrder()
        this.setState({
            pontos: []
        })
    }

    setOpcao = (ponto, descricao) => {
        // console.log(ponto, descricao)
        this.setState({ opcaoRetirarDescricao: descricao })
        this.editPoint(ponto, this.state.endereco,
            this.state.complemento, this.state.lat, this.state.lng, descricao, this.state.tipoEntrega, this.state.notificacaoSmsEmail)

    }

    notificarSmsOuEmail = (ponto, sms) => {

        //    console.log(ponto, sms);

        this.setState({ notificacaoSmsEmail: sms })
        this.editPoint(ponto, this.state.endereco,
            this.state.complemento, this.state.lat, this.state.lng, this.state.opcaoRetirarDescricao,this.state.tipoEntrega, sms)
    }

    setOtimizaRota = (state) => {
        console.log(state)
        this.setState({ otimizaRota: state })

    }

    setTipoEntrega = (tipo) =>{
        this.setState({ tipoEntrega: tipo })
    }

    changeComponent = (proximoPonto) => {

        if (this.state.changeComponent === 1) {
            // this.addPonto();
            this.setState({ changeComponent: 2 });
        } else if (this.state.changeComponent === 2) {
            this.handleChangeFlagEstado();
            this.setState({ changeComponent: 3 });
        } else if (this.state.changeComponent === 3) {
            this.handleClickButtonClearState(proximoPonto);
            this.setState({ changeComponent: 1, numero: '' });
        }

    }

    cleanArrayPoints = () => {
        this.setState({
            pontos: []
        })
    }

    render() {



        var componentStep;
        var route;
        // var routeDetail;

        if (this.state.changeComponent === 2) {
            componentStep = <PointDetail
                pontos={this.state.pontos}
                pontoAtual={this.state.ponto}
                price={this.state.price}
                distance={this.state.distance}
                time={this.state.time}
                descricao={this.state.opcaoRetirarDescricao}
                descricaoFunction={this.setOpcao}
                smsEmail={this.state.notificacaoSmsEmail}
                smsEmailFunction={this.notificarSmsOuEmail}
                // mudaFlag={this.handleChangeFlagEdit}
                mudaFlagEstado={this.handleChangeFlagEstado}
                flagEstado={this.state.flagEstado}
                flag={this.state.flag}
                excluiPonto={this.handleDeleteEndereco}
                isValidated={this.state.isValidated}
                validadorCampos={this.validadorCampos}
                onClick={this.props.includePonto}
                tipoEntrega={this.state.tipoEntrega}
                setTipoEntrega={this.setTipoEntrega}
            />;

            if (!this.state.opcaoRetirarDescricao) {

                route = <button type="button" className="btn btn-primary" disabled={!this.state.opcaoRetirarDescricao}>Próximo</button>
            } else {
                route = <button type="button" className="btn btn-primary" onClick={this.changeComponent}>Próximo</button>
            }

        } else if (this.state.changeComponent === 1) {
            componentStep = <Point
                handleValue={this.state.endereco}
                pontos={this.state.pontos}
                handleValueComplemento={this.state.complemento}
                handleChangeEndereco={this.handleChangeEndereco}
                handleChangeComplemento={this.handleChangeComplemento}
                ponto={this.state.ponto}
                includePonto={this.addPonto}
                handleClickButton={this.handleClickButtonAddPonto}
                price={this.state.price}
                distance={this.state.distance}
                time={this.state.time}
                number={this.state.numero}
                flagEstado={this.state.flagEstado}
                getQuotation={this.getQuotation}
                pontoAtual={this.state.ponto}
                
            />;

            if (this.state.numero || this.state.flagEstado === 'done') {
                route = <button type="button" className="btn btn-primary" onClick={this.changeComponent}>Próximo</button>

            } else {
                route = <button type="button" className="btn btn-primary" disabled={!this.state.numero}>Próximo</button>

            }
        } else if (this.state.changeComponent === 3) {
            componentStep = <Points
                pontos={this.state.pontos}
                handleClickButton={this.handleClickButtonClearState}
                handleClickButtonAddWorkOrder={this.handleClickButtonAddWorkOrder}
                mudaFlag={this.handleChangeFlagEdit}
                excluiPonto={this.handleDeleteEndereco}
                price={this.state.price}
                distance={this.state.distance}
                time={this.state.time}
                otimizaRota={this.state.otimizaRota}
                setOtimizaRota={this.setOtimizaRota}
                changeComponent={this.changeComponent}
                tipoEntrega={this.state.tipoEntrega}
                setTipoEntrega={this.setTipoEntrega}
            />;

            if (this.state.pontos.length <= 1) {
                route = <button type="button" className="btn btn-primary" disabled={this.state.pontos.length <= 1}>Finalizar</button>

            } else {
                //route = <Link to='/cadastro' className="btn btn-primary">Finalizar</Link>

                route = <Link to={{ pathname: '/app/cadastro', state: { quotationData: this.state} }} className="btn btn-primary">Finalizar</Link>
            }
        }


        return (
            /* Novo HTML */
            <div className="helper">
                {/*Including meta tags for Open Graph*/}
                {/* <MetaTags>
                    <title>Conheça {helperData.helperName}. {this.state.primarySkill} próximo da sua região.</title>
                    <meta name="description" content={"Com o Helpie você encontra o melhor " + this.state.primarySkill + ", recebe orçamentos, contrata e paga com cartão de crédito."} />
                    <meta property="og:title" content={"Conheça " + helperData.helperName + ". " + this.state.primarySkill + " próximo da sua região."} />
                    <meta property="og:image" content={helperData.profilePicturePath} />
                </MetaTags> */}
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-9 ml-sm-auto col-lg-5 p-0 pt-5 pedido">
                            {componentStep}
                            <div className="pb-2 px-5 my-5">
                                {route}
                            </div>
                        </div>
                        <div className="col-md-9 col-lg-5 p-0">
                            <Map ref={(x) => { this.map = x; }} lat={this.state.lat} lng={this.state.lng} pontos={this.state.pontos} showMarker={this.state.showMarker} />
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Delivery;



