import NetworkUtil from "../util/NetworkUtil";
import SecurityUtil from "../util/SecurityUtil";
import AuthUtil from "../util/AuthUtil";


var basePath = "users";
class ApiUsers {
    static doUserLogin(login, pass, helpie, onSuccess, onError) {
        var object = {
            login: login,
            pass: SecurityUtil.sha256(pass),
            helpie: helpie 
            
        }


        NetworkUtil.doRequest("POST", basePath + "/login", object, onSuccess, onError)
        
    }

    static getHelpiesByUserId(userId, onSuccess, onError) {
        NetworkUtil.doRequest("GET", basePath + "/" + AuthUtil.getUserId() + "/helpies", null, onSuccess, onError)
        
    }

    static doRegister(email, firstName, lastName, password, ddd, mobilePhone, onSuccess, onError) {

        var object = {
            
                email: email,
                firstName: firstName,
                lastName: lastName,
                password: password,
                ddd:ddd,
                ddi: '+55',
                mobilePhone: mobilePhone
            }
     
        NetworkUtil.doRequest("POST", basePath + "/register", object, onSuccess, onError)
        
    }

    static doForgotPassword(login,onSuccess, onError) {

        var object = {
                login: login
            }
     
        NetworkUtil.doRequest("POST", basePath + "/forgot", object, onSuccess, onError)
        
    }
    
    static getUsers(onSuccess, onError) {
        NetworkUtil.doRequest("GET", basePath + "/" + AuthUtil.getUserId(), null, onSuccess, onError)
        
    }


}

export default ApiUsers


